<template>
  <div class="col-12 no_x" v-if="ready">     
      <div class="col-12 no_x" style="display:flex;flex-direction:row">
            <div class="col-3">
                    Welcome
                    <br/> 
                    <b>{{ dname }}</b>
                    <br/>
                    Please Sign Here <i class="fa fa-arrow-right"></i>
                </div>
                <div class="col-6">
                    <div>
                        <VueSignaturePad
                            id="signature"
                            width="100%"
                            height="200px"
                            ref="signaturePad"
                        />
                        <div style='margin-top:0.5em;margin-left:30%;margin-right:30%'>
                            <button
                                class="btn btn-outline-secondary float-right"
                                @click="undo"
                            >
                                Undo
                            </button>
                            <button
                                class="btn btn-outline-primary float-left"
                                @click="save"
                                :disabled="processing"
                            >
                                Save
                            </button>
                        </div>
                    </div>
                </div> 
            <div class="col-3">&nbsp;</div>   
        </div><br/>
        <iframe class='col-12' :src="url"/>
          <!---->             
</template>

<script>
function getURLParameter(name) {
  return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.search) || [null, ''])[1].replace(/\+/g, '%20')) || null;
}
export default {
    data(){
        return{
            sign:'',
            processing:false,
            proc_id:'',
            dname:'',
            ready:false,
            params: window.location.href.substr(window.location.href.indexOf('?')),
        }
    },
async created(){
    let param = new URLSearchParams(this.params);
     if(param.has('id')){
       this.proc_id = param.get('id');
       this.dname = param.get('dname');
       console.log(this.proc_id);      
            }      
    var baseurl = "src/Procurement/procurement/backend/combined_score_sheet.php?proc_id=";
    this.url = baseurl + this.proc_id;
    //onload data
    var formData = new FormData();

    this.ready = true;
},
    
  name: 'user_setup',
  methods: {
    undo() {
      this.$refs.signaturePad.undoSignature();
    },
    async save() {
      const { isEmpty, data } = this.$refs.signaturePad.saveSignature();
      if(isEmpty){
          this.$alert('Signature board cannot be empty!');
        } else {
            this.processing = true;
            this.sign = data;
            var formData = new FormData();
            formData.append('live','true');
            formData.append('sign_code',this.sign);
            formData.append('proc_id',this.proc_id);
            formData.append('access_name',this.dname);
            await axios.post('Procurement/procurement/backend/update_combine_sign_pw.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='Successfully Signed !'){
                            this.$alert('Successfully Signed ! Redirect to Summary').then(
                                ()=>{
                                    this.processing = false;                                  
                                }
                            )  
                            window.location.href='src/Procurement/procurement/backend/ret_stat_cat4.php?p_id='+this.proc_id+'&name='+this.dname;                       
                        }
                    }
                )
        }  
    }
  }
};
</script>

<style>
#signature {
  border: double 3px transparent;
  border-radius: 5px;
  background-image: linear-gradient(white, white),
    radial-gradient(circle at top left, #4bc5e8, #9f6274);
  background-origin: border-box;
  background-clip: content-box, border-box;
}
</style>



